import { User } from "@/types/types";
import { createContext, Dispatch, ReactNode, SetStateAction, useContext, useEffect, useState } from "react";

type UserContextType = {
    user: User | null;
    setUser: Dispatch<SetStateAction<User | null>>
}


export const AuthContext = createContext<UserContextType | undefined>(undefined);

type childProp = {
    children: ReactNode,
    value: User | null,
}

export const UserContextProvider = ({children, value}: childProp) => {

    // const userData = decryptData(value);
    const [user, setUser] = useState<User | null>(value);

    useEffect(() => {
        setUser(value);
    }, [value])

    return (
        <AuthContext.Provider value={{user, setUser}}>
            {children}
        </AuthContext.Provider>
    );
}

export const useAuth = () => {
    const context = useContext(AuthContext);
    if(!context)
        throw new Error('context not providing...'); 
    
    return context; 
}